<template>
  <form @submit="formSubmit">
    <div class="offcanvas offcanvas-end" tabindex="-1" id="offcanvasForm" aria-labelledby="form-offcanvasLabel">
      <FormHeader :currentId="currentId" editTitle="Edit Currency" createTitle="Add New Currency"></FormHeader>
      <div class="offcanvas-body">
        <div class="row">
          <div class="col-12">
            <div class="form-group">
              <InputField class="col-md-12" :is-required="true" label="Currency Name" placeholder="" v-model="currency_name" :error-message="errors.currency_name" :error-messages="errorMessages['currency_name']"></InputField>
              <InputField class="col-md-12" :is-required="true" label="Currency Symbol" placeholder="" v-model="currency_symbol" :error-message="errors.currency_symbol" :error-messages="errorMessages['currency_symbol']"></InputField>
              <InputField class="col-md-12" :is-required="true" label="Currency Code" placeholder="" v-model="currency_code" :error-message="errors.currency_code" :error-messages="errorMessages['currency_code']"></InputField>
            </div>
            <div class="form-group">
              <div class="d-flex justify-content-between align-items-center">
                <label class="form-label" for="category-status">Is Primary</label>
                <div class="form-check form-switch">
                  <input class="form-check-input" :value="is_primary" :checked="is_primary" name="is_primary" id="is_primary" type="checkbox" v-model="is_primary" />
                </div>
              </div>
            </div>

            <h6><b>Currency Format Settings</b></h6>

            <div class="form-group">
              <label class="form-label" for="label">Currency Position</label>
              <select class="form-select" v-model="currency_position">
                <option value="left">Left</option>
                <option value="right">Right</option>
                <option value="left_with_space">Left With Space</option>
                <option value="right_with_space">Right With Space</option>
              </select>
            </div>

            <div class="form-group">
              <label class="form-label" for="label">Thousand Separatorn</label>
              <input type="text" class="form-control" v-model="thousand_separator" id="label" />
            </div>

            <div class="form-group">
              <label class="form-label" for="label">Decimal Separator</label>
              <input type="text" class="form-control" v-model="decimal_separator" id="label" />
            </div>

            <div class="form-group">
              <label class="form-label" for="label">Number of Decimals</label>
              <input type="number" class="form-control" v-model="no_of_decimal" id="label" />
            </div>

            <div class="form-group">
              <label class="form-label" for="label">Example:- {{ formatCurrencyVue(labelValue, no_of_decimal, decimal_separator, thousand_separator, currency_position, currency_symbol) }}</label>
            </div>
          </div>
        </div>
      </div>
      <FormFooter></FormFooter>
    </div>
  </form>
</template>

<script setup>
import { ref, watch } from 'vue'
import { STORE_URL, EDIT_URL, UPDATE_URL } from '@/vue/constants/currency'
import { useField, useForm } from 'vee-validate'
import { useModuleId, useRequest } from '@/helpers/hooks/useCrudOpration'
import * as yup from 'yup'
import InputField from '@/vue/components/form-elements/InputField.vue'
import FormHeader from '@/vue/components/form-elements/FormHeader.vue'
import FormFooter from '@/vue/components/form-elements/FormFooter.vue'

const emit = defineEmits(['onSubmit'])

// props
const props = defineProps({
  id: { type: Number, default: 0 },
  labelValue: 123456789
})

const { getRequest, storeRequest, updateRequest } = useRequest()

const labelValue = ref(1234567.89)

const currentId = ref(props.id)

// Edit Form Or Create Form
watch(
  () => props.id,
  (value) => {
    currentId.value = value
    if (value > 0) {
      getRequest({ url: EDIT_URL, id: value }).then((res) => {
        if (res.status && res.data) {
          setFormData(res.data)
        }
      })
    } else {
      setFormData(defaultData())
    }
  }
)

// Default FORM DATA
const defaultData = () => {
  errorMessages.value = {}
  return {
    currency_name: '',
    currency_symbol: '$',
    currency_code: '',
    currency_position: 'left',
    no_of_decimal: 2,
    thousand_separator: ',',
    decimal_separator: '.',
    is_primary : false,
  }
}
const formatCurrencyVue = window.formatCurrency

//  Reset Form
const setFormData = (data) => {
  resetForm({
    values: {
      currency_name: data.currency_name,
      currency_symbol: data.currency_symbol,
      currency_code: data.currency_code,
      currency_position: data.currency_position,
      no_of_decimal: data.no_of_decimal,
      thousand_separator: data.thousand_separator,
      decimal_separator: data.decimal_separator,
      is_primary: data.is_primary
    }
  })
}

// Reload Datatable, SnackBar Message, Alert, Offcanvas Close
const reset_datatable_close_offcanvas = (res) => {
  if (res.status) {
    window.successSnackbar(res.message)
    bootstrap.Offcanvas.getInstance('#offcanvasForm').hide()
    setFormData(defaultData())
  } else {
    window.errorSnackbar(res.message)
    errorMessages.value = res.all_message
  }
  emit('onSubmit')
}
const numberRegex = /^\d+$/;
// Validations
const validationSchema = yup.object({
  currency_name: yup.string()
    .required()
    .test('is-string', 'Only strings are allowed', (value) => !numberRegex.test(value)),
    currency_symbol:  yup.string().required(),
    currency_code: yup.string().required()
})

const { handleSubmit, errors, resetForm } = useForm({
  validationSchema
})

const { value: currency_name } = useField('currency_name')
const { value: currency_symbol } = useField('currency_symbol')
const { value: currency_code } = useField('currency_code')
const { value: currency_position } = useField('currency_position')
const { value: no_of_decimal } = useField('no_of_decimal')
const { value: thousand_separator } = useField('thousand_separator')
const { value: decimal_separator } = useField('decimal_separator')
const { value: is_primary } = useField('is_primary')
const errorMessages = ref({})

// Form Submit
const formSubmit = handleSubmit((values) => {
  if (currentId.value > 0) {
    updateRequest({ url: UPDATE_URL, id: currentId.value, body: values }).then((res) => reset_datatable_close_offcanvas(res))
  } else {
    storeRequest({ url: STORE_URL, body: values }).then((res) => reset_datatable_close_offcanvas(res))
  }
})
</script>
